* {
  box-sizing: border-box; }

.clear-fix::after {
  content: '';
  display: block;
  clear: both; }

body {
  font-family: 'pt_sans',arial,sans-serif;
  border-top: 3px solid #f8b910; }
  @media (min-width: 1349px) {
    body {
      width: 1349px; } }
  @media (max-width: 1920px) {
    body {
      width: 100%; } }

a {
  text-decoration: none; }
  a:hover {
    text-decoration: none; }

.container {
  width: 980px;
  margin: auto; }
  @media (max-width: 768px) {
    .container {
      width: 100%;
      padding: 0 15px; } }

a.button {
  color: #faf7f3;
  font: 400 12px / 24px "OpenSans-Regular";
  padding: 7px 14px;
  border: none;
  background-color: #ec644b; }

.button-link, .button-link-red {
  text-transform: uppercase;
  font-size: 12px;
  display: inline-block;
  padding: 15px;
  color: #fff;
  text-align: center;
  cursor: pointer;
  background-color: #1c3d50;
  transition: 0.5s all; }
  .button-link:hover, .button-link-red:hover, .button-link:focus, .button-link-red:focus {
    background-color: rgba(44, 62, 80, 0.58);
    color: #fff; }
  @media (max-width: 560px) {
    .button-link, .button-link-red {
      font-size: 10px;
      padding: 10px 5px; } }

.button-link-red {
  background-color: #ec644b; }
  .button-link-red:hover, .button-link-red:focus {
    background-color: rgba(255, 100, 72, 0.75); }

